"use client";

import { useEffect, useState } from "react";
import { useParams } from "next/navigation";
import TradingViewMiniChart from "@/app/components/TradingViewMiniChart";
import { Card, CardContent } from "@/components/ui/card";
import { Progress } from "@/components/ui/progress";
import { Badge } from "@/components/ui/badge";

type BackendSummary = {
  company_name: string;
  ticker: string;
  summary: string;
  market_cap: number;
  pe_ratio: number;
  range_52w: string;
  sector: string;
};

export default function TickerPage() {
  const { ticker } = useParams();
  const [data, setData] = useState<BackendSummary | null>(null);
  const [error, setError] = useState<string | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (!ticker) return;

    const fetchData = async () => {
      try {
        const res = await fetch(`http://localhost:8000/summary/${ticker}`);
        const json = await res.json();
        if (res.ok) setData(json);
        else setError(json.error || "Unknown error");
      } catch (e) {
        setError("Could not connect to backend.");
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, [ticker]);

  if (loading) return <p className="text-white p-8">Loading...</p>;
  if (error) return <p className="text-red-500 p-8">Error: {error}</p>;
  if (!data) return null;

  // Format summary sections
  const [bizSummary, swotBlock, outlook] = (data.summary || "")
    .split("###")
    .slice(1)
    .map((s) => s.trim());

  return (
    <main className="bg-black text-white px-6 md:px-20 py-24 space-y-16 font-sans">
      {/* Header */}
      <div className="space-y-2">
        <h1 className="text-4xl font-bold text-blue-400 tracking-tight">
          {data.company_name} ({data.ticker})
        </h1>
        <p className="text-sm text-gray-400">Sector: {data.sector}</p>
        <div className="flex flex-wrap gap-4 text-sm text-gray-400">
          <Badge variant="outline">Market Cap: ${Number(data.market_cap).toLocaleString()}</Badge>
          <Badge variant="outline">P/E Ratio: {data.pe_ratio}</Badge>
          <Badge variant="outline">52W Range: {data.range_52w}</Badge>
        </div>
      </div>

      {/* Chart */}
      <Card className="bg-zinc-900 border-zinc-700 p-4">
        <h2 className="text-lg font-semibold text-blue-300 mb-2">ðŸ“ˆ Stock Performance</h2>
        <TradingViewMiniChart symbol={`NASDAQ:${data.ticker}`} />
      </Card>

      {/* Business Summary */}
      <Card className="bg-zinc-900 border-zinc-700">
        <CardContent className="p-6 space-y-4">
          <h2 className="text-xl font-semibold text-blue-300">ðŸ§¾ Business Summary</h2>
          <p className="text-gray-200 whitespace-pre-line">{bizSummary}</p>
        </CardContent>
      </Card>

      {/* SWOT */}
      <Card className="bg-zinc-900 border-zinc-700">
        <CardContent className="p-6 space-y-4">
          <h2 className="text-xl font-semibold text-blue-300">ðŸ“Š SWOT Analysis</h2>
          <p className="text-gray-200 whitespace-pre-line">{swotBlock}</p>
        </CardContent>
      </Card>

      {/* Outlook */}
      <Card className="bg-zinc-900 border-zinc-700">
        <CardContent className="p-6 space-y-4">
          <h2 className="text-xl font-semibold text-blue-300">ðŸ”® Outlook</h2>
          <p className="text-gray-200 whitespace-pre-line">{outlook}</p>
        </CardContent>
      </Card>

      {/* Meters & Visuals (Future Buildout) */}
      {/* <Progress value={60} className="w-full bg-zinc-800" /> */}
    </main>
  );
}
